@import url(https://fonts.googleapis.com/css2?family=Gothic+A1:wght@200;400&display=swap);
body {
  font-family: "Gothic A1";

  margin: 0 auto;
  padding: var(--space-medium);
  max-width: 900 px;
  background: url(/static/media/1.301cbd69.jpg) no-repeat center fixed;
  background-size: cover;
}

.subtitle {
  color: var(--color-grey);
  font-weight: bold;
}

a {
  text-decoration: none;
}

:root {
  /* colors */
  --color-grey: #dddddd;
  --color-grey: #eeeeee;
  /* spaces */
  --space-small: 8px;
  --space-medium: 16px;
  --space-big: 24px;
  --space-extra-big: 48px;
}

.content .conent ul {
  padding-left: var(--space-medium);
}
.content li {
  list-style: square;
}
/* Iphone */
@media (min-width: 400px) {
  .content .grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: var(--space-small);
  }
}
/* Ipad */
@media (min-width: 550px) {
  .content .grid {
    display: grid;
    grid-template-columns: repeat(4, 2fr);
    grid-column-gap: var(--space-medium);
  }
}

/* computer */
@media (min-width: 550px) {
  .content .grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: var(--space-big);
  }
}
.middle-container {
  color: var(--main-color);
  font-size: x-large;
  padding: 10% 30% 10% 10%;
  top: 30%;
  background-color: rgba(211, 211, 211, 0.5);
  border-radius: 25px;
}

.points {
  text-align: center;
  font-family: sans-serif;
  color: #158467;
  background-color: rgba(211, 211, 211, 0.5);
  border-radius: 25px;
}

.container {
  text-align: center;
}
.btn {
  border: 1px solid #158467;
  background: none;
  font-size: 17px;
  font-family: "Gothic A1";
  cursor: pointer;
  transition: 0.8s;
  position: relative;
  overflow: hidden;
  border-radius: 25px;
}
.btn1,
.btn2 {
  color: #158467;
}

.btn1:hover,
.btn2:hover {
  color: #fff;
}

.btn::before {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 0%;
  background: #158467;
  z-index: -1;
  transition: 0.8s;
}
.btn1::before,
.btn3::before {
  top: 0;
  border-radius: 0 0 50% 50%;
}
.btn2::before,
.btn4::before {
  bottom: 0;
  border-radius: 50% 50% 0 0;
}

.btn1:hover::before,
.btn2:hover::before {
  height: 180%;
}

.introduction .avatar {
  border: solid black;
  border-radius: 100px;
  width: 100px;
}

.center {
  text-align: center;
}

.main-menu {
  text-align: center;
  width: 100%;
  margin-top: var(--space-big);
  margin-bottom: var(--space-big);
  font-weight: bold;
  list-style: none;
  width: 100%;
  background-color: rgba(211, 211, 211, 0.5);
  border-radius: 25px;
}

.main-menu a {
  padding: 10px;
  color: #158467;
}

/* .main-menu a:hover {
} */

.footer {
  width: 100%;
  text-align: center;
  font-weight: bold;
  width: 100%;
  background-color: rgba(211, 211, 211, 0.5);
  border-radius: 25px;
  margin-top: var(--space-big);
}
.footer .contact-links {
  padding-left: 0;
  display: inline;
}
.footer .contact-links li {
  list-style: none;
  margin-bottom: var(--space-small);
  display: inline;
  padding: 10px;
}

.footer .contact-links a {
  padding: 5px;
  color: #158467;
}

.picture-item {
  background-color: rgba(211, 211, 211, 0.5);
  border-radius: 25px;
  margin-right: 20px;
  margin-left: 20px;
  margin-bottom: 20px;
  padding: 8px;
}
.picture-item img {
  border-radius: 25px;
  max-width: 100%;
  height: auto;
}
.picture-item .text {
  margin-left: 4px;
}

